<template>
  <div class="face-recognition">
    <div class="flow-diagram">
      <div class="title">正确示意流程</div>
      <!-- <img src="@/static/h5/blSign/speech_flow.gif" alt="流程示意图" /> -->
    </div>
    <div class="tip">
      <div class="content-top">录制时，普通话大声读如下数字</div>
      <div class="number">
        <span v-for="(number, index) in numberArr" :key="index">{{
          number
        }}</span>
      </div>
      <div class="content-bottom">
        正对屏幕 使用<span>前置摄像头</span>录制3~5秒
      </div>
    </div>
    <div class="btn-wrapper">
      <div class="button disabled" v-if="disabledFlag">
        已记住，进入录制（{{ count }}s）
      </div>
      <div class="button" @click="showAgreement">已记住，进入录制</div>
      <div class="button" @click="toFull">已记住，进入录制</div>
    </div>
    <div class="support">由金融壹账通提供技术支持</div>
    <button @click="agree">开始</button>
  </div>
</template>

<script>
import RecordRTC from "recordrtc";
import screenfull from "screenfull";

export default {
  data() {
    return {
      verifycode: "",
      numberArr: ["", "", "", ""],
      count: 3,
      disabledFlag: true,
      timer: null, // 计时器
      bucket: "", // iobs的bucket名称（存放文件的域名称）
      token: "", // 上传需要的token
      key: "", // 文件名，用于上传文件和查询检测结果
      coordinates: "", // 经纬度
      regType: "", // bankValid 从银行卡校验页面过来
      videoRecorder: null,
      stream: null,
      mimeType: "video/webm",
      fileExtension: "webm",
      ua: "",
      senceText: "",
    };
  },
  //监听属性
  computed: {},
  //监控data中的数据变化
  watch: {},
  //生命周期 - 创建完成（可以访问当前this实例）
  created() {
    this.getlocationpoint();
    window.parent.postMessage({ type: "title", title: "人脸识别检测" }, "*");
    this.disabledFlag = false;
  },
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted() {
    this.ua = navigator.userAgent.toLowerCase();
    // 监听全屏退出
    screenfull.onchange(() => {
      // 用户手动退出全屏时视频录制销毁
      if (!screenfull.isFullscreen && this.stream) {
        this.videoRecorder.stopRecording(() => {
          let recordingPlayer = document.querySelector("#recording-player");
          recordingPlayer.innerHTML = "";
          this.stream.stop();
          this.stream = null;
          this.videoRecorder = null;
        });
      }
    });
  },
  beforeDestroy() {
    clearInterval(this.timer);
  },
  //方法集合
  methods: {
    // shootVideo() {
    //   let self = this;
    //   uni.chooseVideo({
    //     sourceType: ["camera"],
    //     success: function (res) {
    //       console.log(res);
    //       if (res.errMsg == "chooseVideo:ok") {
    //         var size = res.size;
    //         console.log("视频size=======");
    //         console.log(size);
    //         if (size / (1024 * 1024) > 20) {
    //           uni.showToast({
    //             title: "录制视频文件过大，请重新录制",
    //             icon: "none",
    //           });
    //           return;
    //         }
    //         self.$store.commit("SET_VIDEO_FILE", res.tempFile);
    //         // todo 跳转到 上传进度页面
    //         console.log("跳转到 上传进度页面=======");
    //         uni.navigateTo({
    //           url:
    //             `upload?coordinates=${self.coordinates}&bucket=${self.bucket}&videoKey=${self.key}&uploadToken=${self.token}&regType=${self.regType}&items=` +
    //             geturlkey("items"),
    //         });
    //       }
    //     },
    //     fail: function (err) {
    //       alert("视频拍摄失败");
    //     },
    //   });
    // },
    createInput() {
      let btnWrapper = document.getElementsByClassName("btn-wrapper")[0];
      if (document.getElementById("videoInput")) return;
      let input = document.createElement("input");
      input.setAttribute("id", "videoInput");
      input.setAttribute("type", "file");
      input.setAttribute("capture", "user");
      input.setAttribute("accept", "video/*");
      input.classList.add("input");
      input.style.opacity = 0;
      input.style.position = "absolute";
      input.style.top = 0;
      input.style.left = 0;
      input.style.width = "100%";
      input.style.height = "100%";
      input.onchange = this.videoUpload;
      input.onclick = this.inputClick;
      btnWrapper ? btnWrapper.appendChild(input) : "";
      document.getElementById("videoInput").click();
    },
    clearInput() {
      let input = document.getElementById("videoInput");
      input && input.remove();
    },
    inputClick() {
    },
    videoUpload(data) {
      console.log(data.target);
      let fileData = data.target.files[0];
      this.dealVideoFile(fileData);
      this.clearInput();
    },
    dealVideoFile(fileData) {
      if (fileData.size / (1024 * 1024) > 20) {
        console.log('录制视频文件过大，请重新录制')
        return;
      }
      this.$store.commit("SET_VIDEO_FILE", fileData);
      document.getElementById("videoInput")
        ? (document.getElementById("videoInput").value = null)
        : "";
      console.log("视频大小", (fileData.size / (1024 * 1024)).toFixed(2) + "M");
      // todo 跳转到 上传进度页面
      console.log("跳转到 上传进度页面=======");
      // uni.navigateTo({
      //   url:
      //     `upload?coordinates=${this.coordinates}&bucket=${this.bucket}&videoKey=${this.key}&uploadToken=${this.token}&regType=${this.regType}&items=` +
      //     geturlkey("items"),
      // });
    },
    countDown() {
      this.timer = setInterval(() => {
        this.count--;
        if (this.count <= 0) {
          clearInterval(this.timer);
          this.disabledFlag = false;
        }
      }, 1000);
    },
    showAgreement() {
      this.$refs.agreement.open();
    },
    agree() {
      this.createInput();
    },
    toFull() {
      let videoWrapper = document.querySelector("#recording-player");
      if (screenfull.isEnabled) {
        screenfull.request(videoWrapper);
      }
      this.recordVideo();
    },
    // 安卓 使用 RecordRTC.js 调用前置摄像头 录制视频
    recordVideo() {
      let video = document.createElement("video");
      let recordingPlayer = document.getElementById("recording-player");
      video.controls = false;
      video.muted = true;
      video.autoplay = true;
      video.playsinline = true;
      video.style.width = "100vw";
      video.style.height = "100vh";
      recordingPlayer.appendChild(video);
      this.captureUserMedia(
        {
          audio: true,
          video: true,
        },
        (stream) => {
          console.log(stream);
          this.stream = stream;
          let videoRecorder = RecordRTC(stream, {
            type: "video",
            disableLogs: false,
            getNativeBlob: false,
            ignoreMutedMedia: false,
            mimeType: this.mimeType,
            video: document.querySelector("#recording-player video"),
          });

          videoRecorder.startRecording();
          this.videoRecorder = videoRecorder;

          setTimeout(() => {
            videoRecorder.stopRecording((url) => {
              let file = null;
              // let url = '';
              let blob = videoRecorder.getBlob();
              let fileName = this.getFileName();
              file = new File([blob], fileName, {
                type: this.mimeType,
              });
              console.log("生成的视频文件=======");
              console.log(file);
              this.dealVideoFile(file);
              url = URL.createObjectURL(file);
              console.log("生成的视频url");
              console.log(url);
              this.stream.stop();
              this.stream = null;
              recordingPlayer.innerHTML = "";
              screenfull.exit(); // 退出全屏
            });
          }, 3000);
        },
        null
      );
    },
    captureUserMedia(mediaConstraints, successCallback) {
      navigator.mediaDevices
        .getUserMedia(mediaConstraints)
        .then((stream) => {
          successCallback(stream);
          let recordingPlayer = document.querySelector(
            "#recording-player video"
          );
          recordingPlayer.poster = "";
          recordingPlayer.addEventListener(
            "loadedmetadata",
            function () {
              setTimeout(() => {
                if (typeof recordingPlayer.play === "function") {
                  recordingPlayer.play();
                }
              }, 500);
            },
            false
          );
          recordingPlayer.srcObject = stream;

          if (typeof recordingPlayer.play === "function") {
            recordingPlayer.play();
          }
          recordingPlayer.addEventListener("ended", function () {
            recordingPlayer.srcObject = stream;
          });
        })
        .catch((error) => {
          alert("录制失败！",error);
        });
    },
    checkFull() {
      var isFull =
        document.fullscreenEnabled ||
        window.fullScreen ||
        document.webkitIsFullScreen ||
        document.msFullscreenEnabled;
      //to fix : false || undefined == undefined
      if (isFull === undefined) {
        isFull = false;
      }
      return isFull;
    },
    getFileName() {
      let d = new Date();
      let year = d.getUTCFullYear();
      let month = d.getUTCMonth();
      let date = d.getUTCDate();
      let timeStamp = d.getTime();
      return (
        "RecordRTC-FaceRecg-" +
        year +
        month +
        date +
        "-" +
        timeStamp +
        "." +
        this.fileExtension
      );
    },
    getlocationpoint() {
      if (navigator.geolocation) {
        navigator.geolocation.getCurrentPosition((position) => {
          let result = position.coords;
          this.coordinates = `X${result.latitude}Y${result.longitude}`;
        });
      }
    },
  },
};
</script>

